@inlineView(`<template>
<tr ref="row" class="level-\${level} \${$this.isOpen?'ui-expanded':''} \${parent.selected==record?'ui-selected':''}" dblclick.delegate="parent.setEditMode(record,col)"  click.delegate="parent.fireSelect(parent.selected = record )">
  <td if.bind="parent.store.groups.length>0 " repeat.for="i of level" class="ui-expander"><div>&nbsp;</div></td>
  <td class="ui-expander"  if.bind="level < parent.store.groups.length || (parent.handleSize>0 && parent.store.groups.length ===0) ">
    <div><a click.trigger="expand($event)"><ui-glyph glyph.bind="$this.isOpen?'glyph-icon-minus':'glyph-icon-plus'"></ui-glyph></a></div>
  </td>

  <td if.bind="parent.store.groups[level]" class="\${getColumn().locked==0?'ui-locked':''}  ui-cell-\${getColumn().dataId}"  css.bind="{'padding-left':'10px'}" colspan="\${getColspan()}" >
    <span class="\${getColumn().class}" innerhtml.bind="getColumn().getGroupValue(record[getColumn().dataId],record)"></span>
  </td>
  <td if.bind="!parent.store.groups[level]" class="\${col.locked==0?'ui-locked':''} \${col.align} ui-cell-\${col.dataId}"  css.bind="{left: col.left+'px'}" repeat.for="col of parent.cols" col.bind="col" parent.bind="parent" record.bind="record" level.bind="level" as-element="ui-dg-cell"></td>

  <td class="filler"><div>&nbsp;</div></td>
  </tr>
  <tr if.bind="$this.isOpen && parent.store.groups.length > 0" containerless >
    <td colspan="\${getColspan() + level + 2}">
      <div class="ui-dg-wrapper" as-element="ui-dg-body" level.bind="level+1" parent.bind="parent" parentrow.bind="$this" data.bind="getSubdata()" css.bind="wrapperCss"></div>
      <div css.bind="{padding:0}">
        <table ref="dgFoot" width.bind="parent.tableWidth" css.bind="{'table-layout': parent.tableWidth?'fixed':'auto' }">
          <colgroup>
            <col repeat.for="i of parent.store.groups.length" width="\${parent.handleSize}" />
            <col repeat.for="col of parent.cols" data-index.bind="$index" width.bind="col.width"/>
            <col/>
          </colgroup>
          <tfoot if.bind="parent.summaryRow"><tr>
            <td repeat.for="i of parent.store.groups.length" class="ui-expander"><div>&nbsp;</div></td>
            <td repeat.for="col of parent.cols" class="\${col.locked==0?'ui-locked':''} \${col.align}" css.bind="{left: col.left+'px'}"><div innerhtml.one-time="col.groupSummary" css.bind="{padding:0}"></div></td>
            <td class="filler"><div>&nbsp;</div></td>
          </tr></tfoot>
        </table>
      </div>
    </td>
    <!--td class="filler"><div>&nbsp;</div></td-->
  </tr>
  <ui-dg-row if.bind="$this.isOpen && parent.store.groups.length === 0" containerless  level.bind="level+1" parent.bind="parent"
    record.bind="rec" repeat.for="rec of getSubdata()"  class="\${$last?'ui-last-inner':''}">
  </ui-dg-row>
</template>`)

@customElement('ui-dg-row')
export class UIDgRow {
  row;
  @bindable() level = 0;
  @bindable() record;
  @bindable() parent;
  @bindable() parentrow;
  wrapperCss = {padding:0,'overflow-x':'hidden','overflow-y':'scroll','font-size':'1em',height:'auto','max-height':'250px',background:'#fff'};

  isOpen = false;
  parentGroupValues = [];
  expand(evt) {  
    this.isOpen = !this.isOpen;
    evt.stopPropagation();
    evt.preventDefault();
    // if(this.isOpen){
      
    // }
    return false;
    
  }

  getColumn(){
    const columnName = this.parent.store.groups[this.level];
    return _.find(this.parent.cols,(col : any)=>col.dataId === columnName);
  }

  getColspan(){
    return (this.parent.cols.length + this.parent.store.groups.length) + this.level + 1;
  }

  getSubdata() {
    console.log((this.parentrow)); 
    if (isFunction(this.record.subdata)) return this.record.subdata(this.record);
    else if( this.record.subdata) return this.record.subdata;
    else{
      let groupValue=[];
      if(this.parentrow){
        this.parentGroupValues = _.union(this.parentrow.parentGroupValues,[this.record[this.parent.store.groups[this.level]]]);
      }else{
        if(this.parentGroupValues.indexOf(this.record[this.parent.store.groups[this.level]]) === -1)
        this.parentGroupValues.push(this.record[this.parent.store.groups[this.level]]);
      }
      let data = this.parent.store.getData(this.level+1,this.parentGroupValues);
      this.parent.cols.forEach(element => {
          element.getSummary(this.parent.summaryRow,'group',this.parent.store,data);
      });
      //  = this.record.parentGroupValues || (this.parentrow?[this.parentrow[this.parent.store.groups[this.level-1]]]:null);
      return data;
      // return this.record.subdata;
    }
  }

  attached(){
    // if(this.parentrow)
    //this.parentGroupValues.push(this.parentrow[this.parent.store.groups[this.level-1]]);
  }
}

