import {autoinject} from 'aurelia-framework';
import {UIEvent} from "../utils/ui-event";
import * as _ from "lodash";
import * as moment from 'moment';

export class BaseDataSource {
  isEmpty = false;
  isLoading = false;
  isLoaded = false;

  dataProperty = 'data';
  totalProperty = 'totalRecords';

  pageProperty = 'page';
  countProperty = 'count';
  sortProperty = 'sort';
  orderProperty = 'order';

  totalPages = 0;
  currentPage = -1;
  recordsPerPage = 25;
  groups = [];

  sortBy = '';
  orderBy = 'asc';

  data = [];
  __original__ = [];
  __filtered__ = [];


  fetchData() { }
  loadPage(pg) { }
  refresh(){ }

  sort(property, order) { }
  filter(props) {
    // array of [{prop: value}]ff
  }
}

export class UILocalDS extends BaseDataSource {

  changes=[];
  constructor(data, opts = {}) {
    super();
    this.__original__ = this.__filtered__ = (data || []);

    Object.assign(this, opts);

    // this.totalPages = Math.ceil(this.__grouped__.length / this.recordsPerPage);
  }

  /**
   * Internal methods
   */
   setTotalPages(data){
    this.totalPages = Math.ceil(data.length / this.recordsPerPage);
  }

  private _sortBy(property,direction){
    let pname = property;
    let factor = direction.match(/^asc*/i) ? -1 : 1;
    // if(this.groups.length>0){
    //   //flatten the groups
    //   let temp = this._recursiveFlat(this.__original__,0);  
    //   console.log(temp);
    // }
    var retvalue = this.__filtered__.sort((a, b) => {
      var textA = a.toUpperCase ? a[property].toUpperCase() : a[property];
      var textB = b.toUpperCase ? b[property].toUpperCase() : b[property];
      textA = moment.isMoment(textA)?textA.format():textA;
      textB = moment.isMoment(textB)?textB.format():textB;
      return (textA < textB) ? factor : (textA > textB) ? -factor : 0;
    });
    
    return retvalue;
  }

  private _groupBy(data,groupindex = 0){
    if(this.groups.length >0){
        var property = this.groups[groupindex];    
        var grouped = _.groupBy(data, property);
        var result : any[] = _.map(grouped, (subdata: any[], prop: any) => { 
          let obj = {};
          obj[property] = prop;
          obj['subdata'] = groupindex === this.groups.length?subdata : this._groupBy(subdata,groupindex+1);
          return obj;
        });
    }else{
      return data;
    }
    if(!property || property ==='') return data;
    console.log(property);
    
    return result;
  }
  /**
   * End internal methods
   */
  
  getData(level,parentGroupValues){
    let grouped = _.clone(this.__filtered__);
    if(level === 0){
      grouped =  _.uniqBy(grouped,this.groups[0]);
    }else{
      for (var index = 0; index < level; index++) {
        var parentValue = parentGroupValues[index];
        grouped = _.chain(grouped).filter(item=>item[this.groups[index]] === parentValue).uniqBy(this.groups[level]).value();
      }
    }
    return grouped;
  }

  fetchData() {
    this.isLoaded = true;
    this.isLoading = true;
    Promise.resolve(this.data = this.getData(0,[]));
    this.isEmpty = this.data.length == 0;
    UIEvent.queueTask(() => this.isLoading = false);
  }

  loadPage(pg = 0) {
    this.isLoaded = true;
    this.isLoading = true;
    let offset = (this.currentPage = pg) * this.recordsPerPage;
    let levelGroupData = this.getData(0,[])
    Promise.resolve(this.data = _.slice(levelGroupData, offset, offset + this.recordsPerPage));
    this.setTotalPages(levelGroupData);
    this.isEmpty = this.data.length == 0;
    UIEvent.queueTask(() => this.isLoading = false);
  }

  filter(props){
    this.isLoading = true;
    let filtered  = this.__original__;
    for (var index = 0; index < props.length; index++) {
      var filter = props[index];
     filtered =  filter.value && filter.value !== ''?_.filter(filtered,item=>item[filter.prop] === filter.value):filtered;
    }
    this.__filtered__ = filtered;
    this.currentPage = this.currentPage === -1 ? this.currentPage : 0;
    // this.__grouped__ = this._groupBy(this.__filtered__);
    Promise.resolve(this.currentPage > -1 ? this.loadPage(this.currentPage) : this.fetchData());
    UIEvent.queueTask(() => this.isLoading = false);
    // console.log(filtered);
    // return filtered;
  }

  sort(property = this.sortBy, order = this.orderBy) {
    this.isLoading = true;
     this.orderBy = order;
    this.sortBy = property;
    this.__filtered__ = this._sortBy(property,order);
    // this.__grouped__ = this._groupBy(this.__filtered__);
    Promise.resolve(this.currentPage > -1 ? this.loadPage(this.currentPage) : this.fetchData());
    UIEvent.queueTask(() => this.isLoading = false);
  }

  getGridSummary(column,type){
    let data = this.__filtered__;
    let retVal;
    switch(type){
      case 'sum': retVal = _.sumBy(data, column); break;
      case 'count': retVal = data.length; break;
      case 'avg': retVal = _['meanBy'](data, column); break;
      default: return null;
    }
    return retVal;
  }

  getGroupSummary(column,type,data){
    // let data = this.getData(level,groupValues);
    let retVal;
    switch(type){
      case 'sum': retVal = _.sumBy(data, column); break;
      case 'count': retVal = data.length; break;
      case 'avg': retVal = _['meanBy'](data, column); break;
      default: return null;
    }
    return retVal;
  }

  pushChanges(record) {
    if (this.changes.indexOf(record) < 0) {
      this.changes.push(record);
    } else {
      _.extend(this.changes[this.changes.indexOf(record)], record);
    }
  }

  undoChanges(record) {
    if (record) {
      if (this.changes.indexOf(record) != -1) {
        for (const prop in record._original_) {
          record[prop] = record._original_[prop];
          record.isDirty = false;
        }
      }
    } else {
      for (var index = 0; index < this.changes.length; index++) {
        var element = this.changes[index];
        for (const prop in element._original_) {
          element[prop] = element._original_[prop];
          element.isDirty = false;
        }
      }
    }
  }


  getChanges() {
    return this.changes;
  }

  commit() {
    var p = new Promise((resolve, reject) => {
      for (var index = 0; index < this.changes.length; index++) {
        var element = this.changes[index];
        delete element.isDirty;
        delete element._original_;
      }
      this.changes = [];
      resolve();
    });
    return p;
  }

  getById(id, pk) {
    return _.find(this.__original__, (rec) => rec[pk] === id);
  }

  refresh(){
    console.log('refresh store');
    
  }
}

export class UIRemoteDS extends BaseDataSource {
  url = 'http://api.domain.com/list';
  buildUrl() { }
}